import React, { useState } from 'react';
import axios from 'axios';
import '../../css/EventPage.css';

function EventForm() {
    const [author, setAuthor] = useState('');
    const [content, setContent] = useState('');

    const handleSubmit = (e) => {
        if (!author || !content) {
            alert(' .');
            e.preventDefault();
            return;
        }

        const newNangman = { author, content };

        axios
            .post('https://special-chapter-ds.com/event/', newNangman)
            .then((response) => {
                console.log(response.data);
            })
            .catch((error) => {
                if (error.response && error.response.status === 422) {
                    alert(
                        '      .   .'
                    );
                }
                console.log(error.response.data);
            });

        //    
        setAuthor('');
        setContent('');
    };

    const handleAuthorChange = (e) => {
        const input = e.target.value;
        if (input.length <= 10) {
            setAuthor(input);
        }
    };

    return (
        <div className="sectionbody2">
            <div className="detailsection">
                <div className="subtitle1">
                    <p> </p> <p>?</p>
                </div>
                <div className="detailtext1">
                    <p>    </p>
                    <p>   !</p>
                    <p> ,  ,  </p>
                    <p> .   .</p>
                    <br />
                    <div className="detailtext2">
                        <p>
                            *    
                            
                        </p>
                        <p>
                            &nbsp;  !   
                             
                        </p>
                        <p>&nbsp;    .</p>
                    </div>
                </div>
            </div>
            <div className="formsection">
                <form className="form_inner" onSubmit={handleSubmit}>
                    <input
                        type="text"
                        value={author}
                        onChange={
                            ((e) => setAuthor(e.target.value),
                            handleAuthorChange)
                        }
                        className="form_writer"
                        placeholder=""
                    />
                    <textarea
                        value={content}
                        onChange={(e) => {
                            const text = e.target.value;
                            if (text.length <= 500) {
                                setContent(text);
                            }
                        }}
                        className="insert"
                        placeholder=" ?"
                    />
                    <button className="submitbtn" type="submit">
                          
                    </button>
                </form>
                <p>*  ,  .</p>
            </div>
        </div>
    );
}

export default EventForm;
