import React, { useState } from 'react';
import axios from 'axios';
import '../css/WriteReviewPage.css';
import back from '../img/back_purple.png';

function WriteReviewPage() {
    const [content, setContent] = useState('');
    const maxCharacters = 90;

    const onClick = () => {
        window.location.href = '/review';
    };

    const handleSubmit = (e) => {
        if (!content) {
            alert(' .');
            e.preventDefault();
            return;
        }

        axios
            .post('https://special-chapter-ds.com/review/', { content })
            .then((response) => {
                console.log(response.data);
            })
            .catch((error) => {
                if (error.response && error.response.status === 422) {
                    alert(
                        '      .   '
                    );
                }
                console.log(error.response.data);
            });

        e.preventDefault();
        window.location.href = '/review';
    };
    const handleChange = (e) => {
        const text = e.target.value;
        if (text.length <= maxCharacters) {
            setContent(text);
        }
    };

    return (
        <div className="writeReview">
            <div className="write-title"> </div>
            <div className="write-top">
                <img
                    src={back}
                    alt=""
                    width="20px"
                    height="20px"
                    className="back-arrow"
                    onClick={onClick}
                />
            </div>

            <div className="write-bottom">
                <form className="review-form" onSubmit={handleSubmit}>
                    <textarea
                        type="text"
                        placeholder="<TAKE:>   !"
                        name="content"
                        className="review-content"
                        maxLength={maxCharacters}
                        onChange={
                            ((e) => setContent(e.target.value), handleChange)
                        }
                        ref={(textarea) => {
                            if (textarea) {
                                textarea.placeholder =
                                    '<TAKE:>   !';
                            }
                        }}
                    />
                    <div className="textCount">
                        {content.length}/{maxCharacters}
                    </div>
                    <button className="review-post-button" type="submit">
                         
                    </button>
                </form>
            </div>
        </div>
    );
}

export default WriteReviewPage;
