import React from 'react';
import Slider from 'react-slick';
import poster1 from '../img/bannerimg1.png';
import poster2 from '../img/bannerimg2.png';
import poster3 from '../img/bannerimg3.jpg';
import poster4 from '../img/bannerimg4.jpg';
import poster5 from '../img/bannerimg5.jpg';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import styled from 'styled-components';
import flower from '../img/main_notice.svg';
import ticket from '../img/main_timetable.svg';
import popcorn from '../img/main_review.svg';
import letter from '../img/main_event.svg';

import '../css/Main.css';

const SliderImg = styled.img`
    width: 375px;
    height: 217px;
    align-self: center;
`;

const Pre = styled.div`
    width: 30px;
    height: 30px;
    position: absolute;
    left: 3%;
    z-index: 3;
`;

const NextTo = styled.div`
    width: 30px;
    height: 30px;
    position: absolute;
    right: 3%;
    z-index: 3;
`;

function Main() {
    const handleClick = (el) => {
        if (el === 1) {
            window.location.href = '/notice';
        } else if (el === 2) {
            window.location.href = '/schedule';
        } else if (el === 3) {
            window.location.href = '/review';
        } else if (el === 4) {
            window.location.href = '/event';
        }
    };

    const settings = {
        dots: true,
        infinite: true,
        autoplay: true,
        autoplaySpeed: 4000,
        cssEase: 'ease-out',
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <NextTo></NextTo>,
        prevArrow: <Pre></Pre>,
    };

    return (
        <div className="mainBody">
            <div className="slider-wrap">
                <Slider {...settings}>
                    <SliderImg src={poster1} />
                    <SliderImg src={poster2} />
                    <SliderImg src={poster3} />
                    <SliderImg src={poster4} />
                    <SliderImg src={poster5} />
                </Slider>
            </div>
            <div className="main-btn-wrap">
                <div className="main-notice" onClick={() => handleClick(1)}>
                    <div className="main-title"></div>
                    <div className="main-content">#_</div>
                    <img className="main-notice-img" src={flower} alt="" />
                </div>
                <div className="main-timetable" onClick={() => handleClick(2)}>
                    <div className="main-title"> </div>
                    <div className="main-content">  </div>
                    <img
                        className="main-timetable-img"
                        src={ticket}
                        alt=""
                    />
                </div>
                <div className="main-review" onClick={() => handleClick(3)}>
                    <div className="main-title"> </div>
                    <div className="main-content">
                        {`<`}TAKE:{`>`}  
                    </div>
                    <img className="main-review-img" src={popcorn} alt="" />
                </div>
                <div className="main-event" onClick={() => handleClick(4)}>
                    <div className="main-title"> </div>
                    <div className="main-content">5 19 </div>
                    <img className="main-event-img" src={letter} alt="" />
                </div>
            </div>
        </div>
    );
}

export default Main;
